<template>
  <v-container fluid>
     <v-layout row wrap>
    
          <v-flex xs12 sm4 v-for="(category,index) in race_categories" :key="category.id">
            <v-card class="mr-1">
              <v-toolbar :color="category.color">
                <v-toolbar-title>{{category.name}}</v-toolbar-title>
                <v-spacer></v-spacer>
                 
                 <h3 class="display-3"  v-if="category.start">
                   <vue-countdown v-on:time-expire="handleTimeExpire" :seconds="getSeconds(category)" :start="true"></vue-countdown> 
                 </h3>
                 <v-btn v-if="!category.start"  @click="startRace(category)" color="info"><v-icon>timer</v-icon> START!</v-btn>
              </v-toolbar>

              <v-card>
                    <v-card-title>
                      <div>
                         <v-tooltip top  class="ml-2" >
                           <span slot="activator"> <v-icon>timer</v-icon> 
                               <b v-if="category.start"> {{ category.start | formatDate }} </b> 
                               <b v-else> {{ category.gunstart }} </b>   
                            </span>
                           <span>Gunstart</span>
                         </v-tooltip> 

                        <v-tooltip top class="ml-2">
                           <span slot="activator"> <v-icon>timer_off</v-icon> 
                              <b v-if="category.end"> {{ category.end | formatDate }} ({{ category.cutoff }} HOURS )</b> 
                              <b v-else> {{ category.cutoff }} HOURS  </b>
                           </span>
                           <span>Cut Off Time</span>
                         </v-tooltip> 
                      </div>
                    </v-card-title>
              </v-card>
 
               <v-list>
               <v-list-tile  
                      v-for="(item) in participants" 
                      :key="item.id" avatar 
                      v-if="item.race_category == category.name">

                      <v-list-tile-action class="text-xs-right">
                        <v-chip outline label :color="item.gender == 'female' ? 'pink' : 'blue-grey darken-4'"><b>{{item.bib_number}}</b></v-chip>
                      </v-list-tile-action>
                    
                      <v-list-tile-content class="pl-1">
                        <v-list-tile-title v-text="`${item.last_name}  ${item.first_name}`"></v-list-tile-title>
                      </v-list-tile-content>

                       <v-list-tile-action class="text-xs-right" >
                          <v-btn v-if="!item.checkin" small @click="checkin(item.id)" >Check-in</v-btn>
                          <v-btn v-if="item.checkin && !category.start" small disabled>FINISH</v-btn>
                          <v-btn v-if="item.checkin && category.start && !item.total_time" 
                                 color="success"
                                 @click="finishLine(item,category)">FINISH</v-btn>
                           
                         <v-chip v-if="item.checkin && category.start && item.total_time">
                         <b>
                          <v-avatar :class="item.gender == 'female' ? 'pink white--text' : 'blue-grey darken-4 white--text'">{{index++}}</v-avatar>
                          {{item.total_time}}
                         </b>
                        </v-chip>

                      </v-list-tile-action>

                </v-list-tile>
                </v-list>
            </v-card>
          </v-flex>


           <v-snackbar top right multiLine v-model="start_race_snackbar" dark> 
                 <p class="title"> Start <b> {{selected_race.name}} </b>  at <b> {{ moment(start_race_date).format("hh:mm:ss A") }} </b> ? </p>
                 <v-btn flat @click.native="setRaceTimestamp(selected_race,start_race_date)">Confirm</v-btn>
                 <v-btn flat @click.native="start_race_snackbar = false">Cancel</v-btn>
          </v-snackbar>


     </v-layout>
  </v-container>
</template>

<script>
  import { db } from '../main'
  import moment from 'moment'
  import VueCountdown from '@dmaksimovic/vue-countdown';

  export default {
    data () {
      return {
        participants: [],
        race_categories: [],
        start_race_snackbar:false,
        selected_race: '',
        start_race_date: '',
      }
    },
    firestore () {
      return {
        participants: db.collection('participants').orderBy('last_name'),
        race_categories: db.collection('race_categories').orderBy('name','desc'),
      }
    },
    components: {
        'vue-countdown': VueCountdown
    },
    methods: {
       moment(){
         return moment()
       },
       showDetails(id){
           this.$router.push('/'+id)
       },
       editCategory(id)
       {
         this.$router.push('/category/'+id)
       },
       checkin(id) {
          db.collection('participants').doc(id).update({checkin:new Date()})      
       },
       startRace(category)
       { 
       
        this.start_race_snackbar = true
        this.selected_race = category
        this.start_race_date = new Date()

       }, 
      setRaceTimestamp(category,start) {

         var cutoff_time =  moment(start).add(category.cutoff, 'hours').format()
         db.collection('race_categories')
           .doc(category.id)
           .update({
              start: moment(start).format(),
              end: cutoff_time,
          })
         this.start_race_snackbar = false
      },
      getSeconds(category) {
          return this.calculateDuration(moment(new Date()).format(),category.end)
      },
      calculateDuration(now,then){
         var diff = moment.duration(moment(then).diff(moment(now)))
         return diff.asSeconds()
      },
      finishLine(participant,category) {

        var duration  = this.calculateDuration(category.start,moment().format())
        var total_time = moment().startOf('day').seconds(duration).format('H:mm:ss')

        db.collection('participants').doc(participant.id).update({total_time:total_time})      
  
      },
      handleTimeExpire () {
            alert('Time is up!');
      }

    },
    filters: {
       formatDate(value) {
          if (value) {
            return moment(String(value)).format('hh:mm:ssA')
          }
       }
    }
    
  }
</script>


