Vue.use(VueFire)

firebase.initializeApp({
	  apiKey: "AIzaSyDna9jNhldWLaipvOwPSfTZoeLMkl5TeIY",
    authDomain: "tibt-2154d.firebaseapp.com",
    databaseURL: "https://tibt-2154d.firebaseio.com",
    projectId: "tibt-2154d",
    storageBucket: "tibt-2154d.appspot.com",
    messagingSenderId: "749115480902",
})


export const db = firebase.firestore()
db.settings({ timestampsInSnapshots: true })
db.enablePersistence()



Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



