<template>
  <v-container fluid grid-list-xl>

      <router-link to="/">
         <v-btn fab dark small absolute right color="primary">
          <v-icon dark>arrow_back</v-icon>
        </v-btn>
      </router-link> 

    <v-slide-y-transition mode="out-in">
       <v-layout row wrap>
          <v-flex xs10 offset-xs1>
            <v-form ref="form" v-model="valid" lazy-validation >
              <v-layout row wrap>

                 <v-flex d-flex xs12 sm4> 
                    <v-text-field label="Category"  v-model="category.name" :rules="requiredRules" required disabled></v-text-field>
                 </v-flex>

                 <v-flex d-flex xs12 sm4> 
                    <v-text-field label="Gunstart"  v-model="category.gunstart" :rules="requiredRules" required></v-text-field>
                 </v-flex>

                  <v-flex d-flex xs12 sm4> 
                    <v-text-field label="Cut Off Time (Hrs)"  v-model="category.cutoff" :rules="requiredRules" required></v-text-field>
                 </v-flex>

                  <v-flex d-flex xs12 sm4> 
                    <v-text-field label="Color"  v-model="category.color" :rules="requiredRules" required></v-text-field>
                 </v-flex>

              </v-layout>

              <v-btn :disabled="!valid"  @click="update(category)" color="primary"> Update </v-btn>
              <v-btn @click="cancel">cancel</v-btn>

            </v-form>
         </v-flex>
       </v-layout>
   </v-slide-y-transition>


  </v-container>
</template>

<script>
  import { db } from '../main'

  export default {
    data () {
     return {
        valid: true,
        snackbar: false,
        category:[],
        requiredRules: [
          v => !!v || 'Field is required'
        ],
     }
    },
    firestore () {
      return {
        category: db.collection('race_categories').doc(this.$route.params.id)
      }
    },
    methods: {
      update (category) {
        if (this.$refs.form.validate()) {
          category.updated_at = new Date()
          db.collection('race_categories').doc(this.$route.params.id).update(category)
          this.snackbar = true
          this.$router.push('/dashboard')
        }
      },
      cancel () {
        this.$router.push('/dashboard')
      }
    }
  }
</script>





