import Vue from 'vue'
import Router from 'vue-router'
import Timer from '@/components/Timer'
import Dashboard from '@/components/Dashboard'
import AddRunner from '@/components/AddRunner'
import ViewRunner from '@/components/ViewRunner'
import ViewRaceCategory from '@/components/ViewRaceCategory'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Timer',
      component: Timer
    },
     {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/add-new-runner',
      name: 'AddRunner',
      component: AddRunner
    },
    {
      path: '/:id',
      name: 'ViewRunner',
      component: ViewRunner
    },
    {
      path: '/category/:id',
      name: 'ViewRaceCategory',
      component: ViewRaceCategory
    }
  ],
  mode:'history'
})



