<template>
  <v-container fluid grid-list-xl>

       <router-link to="/">
         <v-btn fab dark small absolute right color="primary">
          <v-icon dark>arrow_back</v-icon>
        </v-btn>
      </router-link> 

    <v-slide-y-transition mode="out-in">
       <v-layout row wrap>
          <v-flex xs10 offset-xs1>
            <v-form ref="form" v-model="valid" lazy-validation >
              <v-layout row wrap>
                
                 <v-flex d-flex xs12 sm12 md12> 
                  <v-radio-group v-model="participant.race_category" :rules="requiredRules" row> 
                     <v-radio v-for="n in lists.race_category" :key="n" :label="`${n}`" :value="n"></v-radio>
                  </v-radio-group>
                 </v-flex>

                 <v-flex d-flex xs12 sm12 md12> 
                    <v-text-field label="Bib Number"  v-model="participant.bib_number" :rules="requiredRules" required></v-text-field>
                 </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="Last Name"  v-model="participant.last_name" :rules="requiredRules" required></v-text-field>
                 </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="First Name"  v-model="participant.first_name" :rules="requiredRules" required></v-text-field>
                </v-flex>
                
               <v-flex d-flex xs12 sm6 md6> 
                 <v-text-field  label="E-mail"  v-model="participant.email"></v-text-field>
                </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="Contact Number"  v-model="participant.contact_number"></v-text-field>
                </v-flex>
                
                
                <v-flex d-flex xs12 sm3 md3> 
                  <v-radio-group v-model="participant.gender" :rules="requiredRules" row  required> 
                    <v-radio v-for="n in lists.gender"  :key="n" :label="`${n}`" :value="n"></v-radio>
                  </v-radio-group>
                 </v-flex>

                 <v-flex d-flex xs12 sm3 md3> 
                  <v-select v-model="participant.shirt_size" :items="lists.shirt_size" label="Shirt Size"></v-select>
                </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                   <v-text-field label="Nationality"  v-model="participant.nationality"></v-text-field>
                </v-flex>

                  <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="Emergency Contact Person"  v-model="participant.emergency_contact_person"></v-text-field>
                 </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="Emergency Contan Number"  v-model="participant.emergency_contact_number"></v-text-field>
                </v-flex>

              </v-layout>

              <v-btn :disabled="!valid"  @click="submit(participant,snackbar)" color="primary"> submit </v-btn>
              <v-btn @click="clear">clear</v-btn>

            </v-form>
         </v-flex>
       </v-layout>
   </v-slide-y-transition>

    <v-snackbar top right v-model="snackbar" color="green"> 
      New Participant Successfully Added!
      <v-btn flat  @click.native="snackbar = false">Close</v-btn>
    </v-snackbar>

  </v-container>
</template>

<script>
  import { db } from '../main'

  export default {
    data: () => ({
      valid: true,
      snackbar: false,
      participant: {
          first_name: '',
          last_name:'',
          email: '',
          contact_number: '',
          shirt_size: '',
          gender:'',
          race_category: '',
          emergency_contact_person: '',
          emergency_contact_number:'',
          created_at: new Date()
      },
      lists:{
        shirt_size: ['small', 'medium', 'large', 'extra_large'],
        race_category: ['11KM', '21KM', '50KM'],
        gender: ['male', 'female'],
      },
      requiredRules: [
        v => !!v || 'Field is required'
      ],
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
      ],
    }),

    methods: {
      submit (participant, snackbar) {
        if (this.$refs.form.validate()) {
          db.collection('participants').add(participant)
          this.snackbar = true
          this.$router.push('/dashboard')
        }
      },
      clear () {
        
        this.$refs.form.reset()
      }
    }
  }
</script>





