dashboard<template>
  <v-container fluid grid-list-xl>

      <router-link to="/">
         <v-btn fab dark small absolute right color="primary">
          <v-icon dark>arrow_back</v-icon>
        </v-btn>
      </router-link> 

       
         <v-btn fab dark absolute bottom right color="red" class="mb-5" @click="deleteParticipant()">
           <v-icon dark>delete</v-icon>
        </v-btn>
     

    <v-slide-y-transition mode="out-in">
       <v-layout row wrap>
          <v-flex xs10 offset-xs1>
            <v-form ref="form" v-model="valid" lazy-validation >
              <v-layout row wrap>
                
                 <v-flex d-flex xs12 sm12 md12> 
                  <v-radio-group v-model="participant.race_category" row> 
                     <v-radio v-for="n in lists.race_category" :key="n" :label="`${n}`" :value="n"></v-radio>
                  </v-radio-group>
                 </v-flex>

                 <v-flex d-flex xs12 sm12 md12> 
                    <v-text-field label="Bib Number"  v-model="participant.bib_number"></v-text-field>
                 </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="Last Name"  v-model="participant.last_name" :rules="requiredRules" required></v-text-field>
                 </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="First Name"  v-model="participant.first_name" :rules="requiredRules" required></v-text-field>
                </v-flex>
                
               <v-flex d-flex xs12 sm6 md6> 
                 <v-text-field  label="E-mail"  v-model="participant.email"  :rules="emailRules" required></v-text-field>
                </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="Contact Number"  v-model="participant.contact_number"></v-text-field>
                </v-flex>
                
                
                <v-flex d-flex xs12 sm3 md3> 
                  <v-radio-group v-model="participant.gender" row > 
                    <v-radio v-for="n in lists.gender"  :key="n" :label="`${n}`" :value="n"></v-radio>
                  </v-radio-group>
                 </v-flex>

                 <v-flex d-flex xs12 sm3 md3> 
                  <v-select v-model="participant.shirt_size" :items="lists.shirt_size" label="Shirt Size"></v-select>
                </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                   <v-text-field label="Nationality"  v-model="participant.nationality"></v-text-field>
                </v-flex>

                  <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="Emergency Contact Person"  v-model="participant.emergency_contact_person"></v-text-field>
                 </v-flex>

                 <v-flex d-flex xs12 sm6 md6> 
                    <v-text-field label="Emergency Contan Number"  v-model="participant.emergency_contact_number"></v-text-field>
                </v-flex>

              </v-layout>

              <v-btn :disabled="!valid"  @click="update(participant,snackbar)" color="primary"> Update </v-btn>
              <v-btn @click="cancel">cancel</v-btn>

            </v-form>
         </v-flex>
       </v-layout>
   </v-slide-y-transition>

    <v-snackbar top right v-model="snackbar" color="error"> 
       Are you sure, you want to delete &nbsp;<b> {{participant.last_name}} {{ participant.first_name }} </b> ?
       <v-btn flat @click.native="confirmedDelete()">Delete</v-btn>
       <v-btn flat @click.native="snackbar = false">Cancel</v-btn>
    </v-snackbar>

  </v-container>
</template>

<script>
  import { db } from '../main'

  export default {
    data () {
     return {
        valid: true,
        snackbar: false,
        participant:[],
        lists:{
          shirt_size: ['small', 'medium', 'large', 'extra_large'],
          race_category: ['11KM', '21KM', '50KM'],
          gender: ['male', 'female'],
        },
        requiredRules: [
          v => !!v || 'Field is required'
        ],
        emailRules: [
          v => !!v || 'E-mail is required',
          v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
        ],
     }
    },
    firestore () {
      return {
        participant: db.collection('participants').doc(this.$route.params.id)
      }
    },
    methods: {
      update (participant, snackbar) {
        if (this.$refs.form.validate()) {
          participant.updated_at = new Date()
          db.collection('participants').doc(this.$route.params.id).update(participant)
          this.snackbar = true
          this.$router.push('/dashboard')
        }
      },
      deleteParticipant () {
         this.snackbar = true
      },
      confirmedDelete () {
         db.collection('participants').doc(this.participant.id).delete()
        this.$router.push('/dashboard')
      },
      cancel () {
        this.$router.push('/dashboard')
      }
    }
  }
</script>





