<template>
  <v-container fluid>
      <router-link to="/add-new-runner">
         <v-btn fab dark absolute right bottom color="blue mb-4">
          <v-icon dark>add</v-icon>
        </v-btn>
      </router-link> 
     
     <v-layout row wrap>
    
          <v-flex xs4 v-for="category in race_categories" :key="category.id">

            <v-card class="mr-1">
             
              <v-toolbar :color="category.color">
                <v-toolbar-title>{{category.name}}</v-toolbar-title>
                <v-spacer></v-spacer>
                    <v-icon>accessible_forward</v-icon>
                     <v-btn v-if="category.start"  color="error" small @click="resetTimer(category.id)">Reset</v-btn>
              </v-toolbar>

               <v-card>
                    <v-card-title>
                      <div>
                        <span class="grey--text">May 27, 2018</span>                          

                        <v-tooltip top class="ml-1">
                          <span slot="activator"> <v-icon>timer</v-icon> <b> {{ category.gunstart }} </b>  </span>
                          <span>Gun Start</span>
                        </v-tooltip> 

                        <v-tooltip top class="ml-2">
                           <span slot="activator"> <v-icon>timer_off</v-icon> <b> {{ category.cutoff }} HOURS </b>  </span>
                           <span>Cut Off Time</span>
                         </v-tooltip> 
                      
                         <v-btn flat color="orange" small @click="editCategory(category.id)"><v-icon small>edit</v-icon>EDIT</v-btn>
                      </div>
                    </v-card-title>
                     
              </v-card>

              
               <v-list>
                <v-list-tile v-for="item in participants" :key="item.id" avatar @click="showDetails(item.id)" v-if="item.race_category == category.name">
                      <v-avatar :color="item.gender == 'female' ? 'pink' : 'black'" size="30px">
                        <span class="white--text">{{item.last_name.charAt(0).toUpperCase() }}</span>
                      </v-avatar>
                    
                      <v-list-tile-content class="pl-1">
                        <v-list-tile-title v-text="`${item.last_name}  ${item.first_name}`"></v-list-tile-title>
                      </v-list-tile-content>
                  
                       <v-list-tile-action class="text-xs-right">
                        <v-chip label outline :color="item.gender == 'female' ? 'pink' : 'black'" 
                          >{{item.bib_number}}
                        </v-chip>
                      </v-list-tile-action>

                  </v-list-tile>
                 </v-list>
            </v-card>
          </v-flex>
    </v-layout>

       <v-snackbar top right v-model="reset_race_snackbar" color="error"> 
             <p class="title"> Confirm Reset? </p>
             <v-btn flat @click.native="confirmTimerReset(selected_doc_id)">RESET</v-btn>
             <v-btn flat @click.native="reset_race_snackbar = false">Cancel</v-btn>
       </v-snackbar>

  </v-container>
</template>

<script>
  import { db } from '../main'
  import moment from 'moment'

  export default {
    data () {
      return {
        participants: [],
        race_categories: [],
        reset_race_snackbar: false,
        selected_doc_id:'',
      }
    },
    firestore () {
      return {
        participants: db.collection('participants').orderBy('last_name'),
        race_categories: db.collection('race_categories').orderBy('name','desc')
      }
    },
    methods: {
       showDetails(id){
           this.$router.push('/'+id)
       },
       editCategory(id)
       {
         this.$router.push('/category/'+id)
       },
       resetTimer(id) {
        this.reset_race_snackbar = true
        this.selected_doc_id = id
       },
       confirmTimerReset(id) {
         this.reset_race_snackbar = false
           db.collection('race_categories')
           .doc(id)
           .update({
              start: '',
              end: '',
          })
       }


    },
    filters: {
       formatDate(value) {
          if (value) {
            return moment(String(value)).format('hh:mm:ssA')
          }
       }
    }
    
  }
</script>


